<template>
  <div class="col-md-6 col-md-offset-3" id="translateOutput">
    <h2>{{translatedText}}</h2>
  </div>
</template>

<script>


export default {

  name: 'translateOutput',
  props:[
    "translatedText"
  ]
  
}
</script>

<style>
  #translateOutput{
    border: 1px solid #ccc;
    color: #a66;
    border-radius: 10px;
    font-size: 18px;
  }
</style>



